export default {
	data() {
		return {
			id: 0,
			detail: {}
		}
	},
	onLoad(options) {
		this.id = options.id
		this.getDetailNews()
	},
	methods: {
		async getDetailNews () {
			const res = await this.$myRequest({
				url: '/api/getnew/'+this.id
			})
			this.detail = res.data.message[0]
		}
	}
}
